.header {
  width: 100%;
  z-index: 400;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 5px -1px rgba(50, 50, 93, .25), 0 1px 3px -1px rgba(0, 0, 0, .3);
}

.header .container {
  min-height: 80px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header .container .logo-icon {
  width: 100%;
  max-width: 95px;
  height: 100%;
  max-height: 52px;
}

.header .container .nav-site {
  color: #000;
  background-color: #fff;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.17;
}

@media screen and (min-width: 960px) {
  .header .container .nav-site {
    padding-left: 20px;
  }
}

.header .container .nav-site .list {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  row-gap: 20px;
  margin-top: 40px;
  margin-left: 40px;
  display: flex;
}

@media screen and (min-width: 960px) {
  .header .container .nav-site .list {
    width: auto;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0 20px;
    margin: 0;
    display: flex;
  }
}

.header .container .nav-site .list .item {
  width: 100%;
  max-width: 200px;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

@media screen and (min-width: 960px) {
  .header .container .nav-site .list .item {
    width: auto;
    display: block;
  }
}

.header .container .nav-site .list .link {
  width: 100%;
  text-align: center;
  padding: 7px 0;
  display: block;
  position: relative;
}

.header .container .nav-site .list .link:before {
  content: "";
  transform-origin: 100%;
  z-index: 1;
  background: rgba(17, 38, 58, .5);
  border-radius: 6px;
  transition: transform .5s ease-in-out;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

.header .container .nav-site .list .link:hover:before, .header .container .nav-site .list .link:focus:before {
  transform-origin: 0;
  transform: scaleX(1);
}

@media screen and (min-width: 960px) {
