}

.buttons-container .mobil-button.hide {
  display: none;
}

@media screen and (max-width: 960px) {
  .mobil-container {
    max-width: 300px;
    width: 100%;
    height: 100vh;
    z-index: 199;
    visibility: hidden;
    opacity: 0;
    background-color: #fff;
    transition: all .25s linear;
    position: absolute;
    top: 80px;
    right: 0;
    overflow: scroll;
    transform: translateX(305px);
  }
}

.mobil-container.is-open {
  visibility: visible;
  opacity: 1;
  transition: all .25s linear;
  transform: translateX(0);
}

